import keys from '../keys.json';

export default {
  data: function() {
    return {
      isSignedIn: false,
      files: [],
      user: {
        name: '',
        img: ''
      }
    };
  },

  mounted() {
    this.handleClientLoad();
  },

  methods: {
    handleClientLoad() {
      gapi.load('client:auth2', this.initClient);
    },

    initClient() {
      gapi.client
        .init({
          apiKey: keys.API_KEY,
          clientId: keys.CLIENT_ID,
          discoveryDocs: keys.DISCOVERY_DOCS,
          scope: keys.SCOPES
        })
        .then(() => {
          // Listen for sign-in state changes.
          const googleAuth = gapi.auth2.getAuthInstance();

          googleAuth.isSignedIn.listen(this.updateSigninStatus);

          this.isSignedIn = googleAuth.isSignedIn.get();
          this.updateSigninStatus(this.isSignedIn);
        });
    },

    updateSigninStatus(isSignedIn) {
      console.log('Status: ', isSignedIn);
      if (isSignedIn) this.listFiles();
    },

    handleAuthClick(event) {
      const user = gapi.auth2.getAuthInstance().signIn();
      console.log(user);
      // prompt: 'select_account'
      // this.user.name = user.rW.w3.ig;
      // this.user.img = user.rW.w3.Paa;
      // this.isSignedIn = true;
      this.isSignedIn = gapi.auth2.getAuthInstance().isSignedIn.get();
    },

    handleSignoutClick(event) {
      gapi.auth2.getAuthInstance().signOut();
      this.isSignedIn = gapi.auth2.getAuthInstance().isSignedIn.get();
    },

    appendPre(message) {
      var pre = document.getElementById('content');
      var textContent = document.createTextNode(message + '\n');
      pre.appendChild(textContent);
    },

    listFiles() {
      gapi.client.drive.files
        .list({
          pageSize: 10,
          fields: 'nextPageToken, files(id, name)'
        })
        .then(
          response => {
            this.files = response.result.files.map(e => e.name);
          }
          // function(response) {
          //
          //   this.appendPre('Files:');
          //   var files = response.result.files;
          //   if (files && files.length > 0) {
          //     for (var i = 0; i < files.length; i++) {
          //       var file = files[i];
          //       this.appendPre(file.name + ' (' + file.id + ')');
          //     }
          //   } else {
          //     this.appendPre('No files found.');
          //   }
          // }.bind(this)
        );
    },

    nik() {
      console.log('des bails');
    }
  }
};



