	<!-- <h6>Hello {{user.rW.w3.ig}}</h6>
	<img src="user.rW.w3.Paa" /> -->
	<!--Add buttons to initiate auth sequence and sign out-->
	<button v-if="!isSignedIn" @click="handleAuthClick">Authorize</button>
	<button v-else @click="handleSignoutClick"> Sign Out</button>

