<script>
import keys from '../keys.json';
import google from '../mixins/google.js';

export default {
  name: `gSigninButton`,
  mixins: [google],

  data: function() {
    return {};
  },
  computed: {},
  methods: {},
  components: {}
};
</script>

<style>
.btn-sign-in {
  background: #fff;
  font: 16px/22px Roboto;
  padding: 4px 8px;
  border: 1px solid #ccc;
  display: inline-block;
  cursor: pointer;
}

.displayed {
	display: block;
}

.notDisplayed {
	display: none;
}

.test {
	border: 5px solid black;
}
</style>



